import React from 'react';
import { Layer, Rect, Stage } from 'react-konva';
import { useRecoilState } from 'recoil';
import { nameState } from '../../atoms';
import { Button } from '../Button/Button';
import { SpeechBox } from '../SpeechBox/SpeechBox';
import './Game.scss';

export const Game = () => {
  const [name, setName] = useRecoilState(nameState);

  return (
    <div className={'game'}>
      <div className={'controls'}>
        <Button onClick={() => setName('')}>Clear Name</Button>
        <SpeechBox text={`Hello ${name}. Let's get started!`} />
      </div>
      <Stage height={1000} width={1000}>
        <Layer>
          <Rect
            x={0}
            y={0}
            width={1000}
            height={1000}
            fill='rgba(255, 255, 255, 0.1)'
            shadowBlur={10}
          />
          <Rect
            x={20}
            y={50}
            width={100}
            height={100}
            fill='red'
            shadowBlur={20}
            onClick={() => alert('Great job!')}
          />
        </Layer>
      </Stage>
    </div>
  );
};
